import {ChangeDetectionStrategy, Component, Input} from "@angular/core";
import {Logger} from "@util/logger";

/**
 * Component which displays illustrated messages such as info for the page, lack of data, etc.
 * Each type of message has own IllustratedMessageType.
 * The image on top of message depends on the type. If add new type, define a picture for it.
 * Message is a text right below the image
 * Content between tags if any is displayed at the bottom. This is the place to add custom functionality to the
 * component i.e functionality for new instance, configure, etc.
 */
@Component({
   selector: "vsan-illustrated-message",
   templateUrl: "illustrated-message.html",
   styleUrls: ["illustrated-message.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IllustratedMessageComponent {
   private readonly MISSING_DATA_IMAGE_CLASS: string = "missing-data-image";
   private readonly ACCESS_DENIED_IMAGE_CLASS: string = "access-denied-image";
   private readonly MISSING_SELECTION_CLASS: string = "missing-selection-image";
   private readonly FILTER_NO_RESULT_CLASS: string = "filter-no-result";

   @Input()
   type: IllustratedMessageType = IllustratedMessageType.MISSING_DATA;

   @Input()
   message: string;

   getImageClass(): string {
      switch (this.type) {
         case IllustratedMessageType.MISSING_DATA:
            return this.MISSING_DATA_IMAGE_CLASS;
         case IllustratedMessageType.ACCESS_DENIED:
            return this.ACCESS_DENIED_IMAGE_CLASS;
         case IllustratedMessageType.MISSING_SELECTION:
            return this.MISSING_SELECTION_CLASS;
         case IllustratedMessageType.FILTER_NO_RESULT:
            return this.FILTER_NO_RESULT_CLASS;
         default:
            Logger.warn("Not handled type: " + this.type);
      }
   }
}

/**
 * The type of the message.
 */
export enum IllustratedMessageType {
   // If add new type, add also handling for it in getImageClass() method.
   MISSING_DATA,
   ACCESS_DENIED,
   MISSING_SELECTION,
   FILTER_NO_RESULT,
}
